import React, { Fragment } from "react";
import CityCard from "./CityCard";

const CityList = props => (
  <Fragment>
    <div className="row">
      <div className="col-12">
        <h3>{props.title}</h3>
        {props.showSubtitle === true && props.subtitle !== "" && (
          <h6 className="text-muted">
            Search result for keword '{props.subtitle}'
          </h6>
        )}
      </div>
    </div>
    <div className="row">
      {props.cities == null ? (
        <div className="col">
          <p>Loading</p>
        </div>
      ) : (
        _renderCityList(props.cities)
      )}
    </div>
  </Fragment>
);

const _renderCityList = cities => {
  if (cities.length > 0) {
    return cities.map(city => <CityCard key={city.id} city={city} />);
  } else {
    return (
      <div className="col">
        <p className="text-danger">Data not found</p>
      </div>
    );
  }
};

export default CityList;
